<template>
    <div>
        <div class="kd-page-title">
            选择查询日期
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="开始时间：">
                    <el-date-picker
                        class="year"
                        v-model="startTime.year"
                        type="year"
                        prefix-icon="a"
                        :editable="false"
                        value-format="yyyy"
                    />
                    <el-select v-model="startTime.month">
                        <el-option
                            v-for="item in months"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        />
                    </el-select>
                </el-form-item>
                <el-form-item label="结束时间：">
                    <el-date-picker
                        class="year"
                        v-model="endTime.year"
                        type="year"
                        prefix-icon="a"
                        :editable="false"
                        value-format="yyyy"
                    />
                    <el-select v-model="endTime.month">
                        <el-option
                            v-for="item in months"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        />
                    </el-select>
                </el-form-item>
            </el-form>
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <!-- <kd-button icon="kedaIcon-circleleft" text="上一步" :buttonHandle="goUp"></kd-button> -->
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!noCrimeBusinessType"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import moment from 'moment';

export default {
    name: 'ExitRecordsUpload',
    data () {
        return {
            step: 0,
            nowImg: '',
            isLoad: false,
            noCrimeMaterialPhoto: this.$store.state.innocent.noCrimeMaterialPhoto,
            noCrimeBusinessType: '',
            startTime: {
                year: '',
                month: ''
            },
            endTime: {
                year: '',
                month: ''
            },
            months: [
                {
                    label: '一月',
                    value: '01'
                },
                {
                    label: '二月',
                    value: '02'
                },
                {
                    label: '三月',
                    value: '03'
                },
                {
                    label: '四月',
                    value: '04'
                },
                {
                    label: '五月',
                    value: '05'
                },
                {
                    label: '六月',
                    value: '06'
                },
                {
                    label: '七月',
                    value: '07'
                },
                {
                    label: '八月',
                    value: '08'
                },
                {
                    label: '九月',
                    value: '09'
                },
                {
                    label: '十月',
                    value: '10'
                },
                {
                    label: '十一月',
                    value: '11'
                },
                {
                    label: '十二月',
                    value: '12'
                }
            ]
        };
    },
    computed: {
        ...mapState('permanent', {
            dialog: 'highPhotoDialog',
            lightWebSocket: 'lightWebSocket',
            highPhotoDev: 'highPhotoDev',
            highPhotoImg: 'highPhotoImg'
        }),
        ...mapState('innocent', {
            noCrimeBusinessTypes: 'noCrimeBusinessTypes'
        })
    },
    mounted () {
        // setTimeout(() => {
        // 	this.isLoad = true;
        // }, 3000);
        // this.dialog.html_loaded('one');
    },
    beforeDestroy () {
        // 关闭高拍仪主视频
        // this.dialog.get_actionType('closeSignal');
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('innocent', ['setNoCrimeMaterialPhoto']),

        // 进入第一步
        async goToIndex () {
            this.$wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
            this.step = 0;
        },
        // 进入第二步
        goToPhoto () {
            if (this.isLoad) {
                this.$wsSend(this.lightWebSocket, 'lightCtrl@B6:ON');
                this.step = 1;
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (this.startTime.year && this.startTime.month && this.endTime.year && this.endTime.month) {
                const startTime = this.startTime.year + '-' + this.startTime.month;
                const endTime = this.endTime.year + '-' + this.endTime.month;
                // 检测时间是否正确
                if (moment(startTime).isBefore(endTime)) {
                    this.changeStep('down');
                } else {
                    // 校验信息
                }
            } else {
                // 校验信息
            }
        },
        getTitle () {
            if (this.step === 0) {
                return '办证事由及材料';
            } else if (this.step === 1) {
                return '请将证明放置于照片识别区保持不动';
            } else if (this.step === 2) {
                return '拍摄完成';
            }
        },
        // 拍照，进入第三部
        startScan () {
            this.nowImg = this.highPhotoImg;
            this.step = 2;
        },
        // 保存数据，并返回第一步
        save () {
            this.noCrimeMaterialPhoto.push(this.nowImg);
            this.setNoCrimeMaterialPhoto(this.noCrimeMaterialPhoto);
            this.$wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
            this.step = 0;
        },
        removeImg (index) {
            this.noCrimeMaterialPhoto.splice(index, 1);
            this.setNoCrimeMaterialPhoto(this.noCrimeMaterialPhoto);
        }
    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: center;

        ::v-deep .el-form-item__content {
            margin-right: 10vw;
        }

        ::v-deep .el-select {
            height: 72px;
            width: 180px;
            box-sizing: border-box;
            border: 1PX solid #fff;/*no*/
            border-radius: 6px;

            .el-input__inner {
                height: 72px;
                background: rgba(255, 255, 255, 0.8);
                border-radius: 6px;
                border: 1PX solid #fff;/*no*/
                font-size: 26px;
                color: #8e9cb9;
                line-height: 37px;
            }
        }

        ::v-deep .el-date-editor {
            height: 72px;
            width: 70%;
            box-sizing: border-box;
            border: 1PX solid #fff;/*no*/
            border-radius: 6px;
            cursor: pointer;

            .el-input__inner {
                height: 72px;
                background: rgba(255, 255, 255, 0.8);
                border-radius: 6px;
                border: 1PX solid #fff;/*no*/
                font-size: 26px;
                color: #8e9cb9;
                line-height: 37px;
            }
        }

        .year {
            width: 360px;
        }

        .month {
            width: 180px;
        }

        .imgListsWrap {
            display: flex;

            .uploadImg {
                margin-right: 20px;
                border-radius: 6px;
                border: 1PX solid rgba(242, 249, 255, 1);/*no*/
                width: 13vw;
                height: 13vw;
                position: relative;

                .preImg {
                    width: 100%;
                    height: 100%;
                    border-radius: 6px;
                }

                .delImg {
                    position: absolute;
                    top: -15px;
                    right: -15px;
                    z-index: 3;
                    width: 30px;
                    height: 30px;
                    background: url("../../../../assets/images/del.png");
                    background-size: 100% 100%;
                }
            }
        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            width: 13vw;
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85baf0;
                text-align: center;
                line-height: 13vw;
            }
        }
    }
</style>
