<template>
    <tip v-if="visibleTip"/>
    <div
        v-else
        class="kd-bg-wrapper"
    >
        <step-head
            :steps-item="steps"
            :is-visiable="visibleSteps"
        />
        <jurisdiction/>
        <prompt-business/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <upload v-if="currentStep === steps.findIndex(step=>step.key === 'timelist')"/>
        <!-- <mail v-if="currentStep === steps.findIndex(step=>step.key === 'mail')"></mail> -->
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="exitRecordComplete"
            foot-texts="温馨提示：公安出入境部门将会对提交信息进行审核，审核结果将以短信告知，若审核通过， 请携带身份证前往原自助设备刷身份证打印领取出入境记录。"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import personInfo from './components/person-info';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import checkPhone from './components/check-phone';
import upload from './components/upload';
// import mail from './components/mail';
import complete from '@/components/complete';
import stepHead from '@/components/step-head';
import process from '@/components/process';
// import { cloneDeep } from 'lodash';

export default {
    name: 'ExitRecordsIndex', // 出境记录查询
    components: {
        personInfo,
        identifyCardPage,
        faceYitu,
        contrast,
        checkPhone,
        upload,
        complete,
        stepHead,
        process
    },
    data () {
        return {
            exitRecordComplete: {
                name: '张雪宁',
                serialNumber: '$Sdsss1231313',
                applyTime: '2019-4-25'
            }
        };
    },
    computed: {
        ...mapState('common', {
            visibleTip: 'visibleTip',
            visibleSteps: 'visibleSteps',
            steps: 'steps',
            currentStep: 'currentStep',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        }),
        ...mapState('exitRecord', {})
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async handleBusiness () {

        },
        async handleContrast () {
            this.changeStep('down');
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '选择查询日期',
                isActive: false,
                visible: true,
                key: 'timelist'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: true,
                key: 'checkPhone'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};
</script>
