<template>
    <div>
        <div class="kd-page-title">
            手机校验
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <verify-code
                @getVerifyCode="getVerifyCode"
                ref="verifyCode"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { noCrimeGetVerifyCode, noCrimeVerifyCode } from '../../../../service/human/people';
import verifyCode from '@/components/verify-code';

export default {
    name: 'ExitRecordsCheckPhone',
    components: { verifyCode },
    computed: {
        ...mapState('innocent', {
            noCrimeBusinessTypes: 'noCrimeBusinessTypes'
        })
    },
    methods: {
        ...mapMutations('innocent', ['setNoCrimeRegisterInfo', 'setNoCrimePhone']),
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async getVerifyCode () {
            const res = await noCrimeGetVerifyCode({
                phone: this.$refs.verifyCode.item.phoneNumber
            });
            if (res && res.code === '0') {
                console.log('获取验证码成功');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (this.$refs.verifyCode.submission()) {
                const res = await noCrimeVerifyCode({
                    phone: this.$refs.verifyCode.item.phoneNumber,
                    code: this.$refs.verifyCode.item.verifyCode
                });
                if (res && res.code === '0') {
                    this.setNoCrimePhone(this.$refs.verifyCode.item.phoneNumber);
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
